@import url(https://use.typekit.net/syr7jme.css);
/***

====================================================================
    Footer
====================================================================

 ***/
.main-footer {
    position: relative;
}

.widget-content img.slogan {
    float: right;
    margin-top: -80px;
    margin-right: -40px;
    width: 300px;
}

.main-footer .has-line-center:after {
    width: 100%;
    background: #F3F3F3;
    content: '';
    display: block;
    height: 2px;
    margin: 40px auto 60px;
}

.main-footer h3 {
    color: #F3F3F3;
    font-size: 80px;
    text-transform: uppercase;
    position: relative;
    line-height: 1;
    margin-bottom: 0;
    font-family: acumin-pro-extra-condensed, sans-serif;
    font-weight: 800;
    font-style: normal;
}

.widgets-section {
    position: relative;
    padding: 80px 0;
    background: url(../../static/media/Crowd.402032a9.jpg) bottom no-repeat;
    background-size: cover;
}

.footer-widget {
    position: relative;
}

.about-widget .text {
    position: relative;
    margin-bottom: 25px;
    font-size: 38px;
    line-height: 1.25;
    color: #F3F3F3;
    max-width: 860px;
}


.social-links {
    position: relative;
}

.social-links li {
    position: relative;
    display: inline-block;
    vertical-align: middle;
}

.social-links li:last-of-type {
    margin-left: 20px;
}

.social-links li a {
    position: relative;
    display: block;
    text-align: center;
}

.social-links li a:hover {
    cursor: pointer;
}

.footer-bottom {
    position: relative;
    padding: 18px 0;
    color: #efefef;
    line-height: 1;
    font-size: 24px;
    height: 124px;
}

.footer-bottom .container {
    padding-top: 30px;
}

@media screen and (max-width: 414px) {
    .widget-content .text {
        max-width: 360px;
        font-size: 20px;
    }
    .widget-content img.slogan {
        float: right;
        margin-top: 0;
        margin-right: 0;
        width: 200px;
    }
    .copyright-text{
        font-size: 16px;
    }
}

