#body-content {
    max-width: 800px;
    margin: 60px auto 110px;

}

#body-content p {
    font-family: "MANIC Alternates 3", sans-serif;
    font-size: 38px;
    margin-bottom: 60px;
}

span {
    position: relative;
}

.f-md {
    font-size: 50px;
}

.f-lg {
    font-size: 52px;
}

.f-xl {
    font-size: 60px;
}


.manic-shine img {
    position: absolute;
    top: -60px;
    left: -40px;

}

.manic-line img {
    position: absolute;
    bottom: -16px;
    left: 0;
}

.manic-emphasis img {
    position: absolute;
    bottom: -28px;
    left: -44px;
}

.manic-smiley img {
    position: absolute;
    bottom: -30px;
    right: -88px;
}


.manic-circle img {
    position: absolute;
    left: -30px;
    top: -30px;
}

.manic-circle-alt img {
    position: absolute;
    left: -40px;
    top: -40px;
}

.manic-emphasis-alt img {
    position: absolute;
    bottom: -10px;
    left: -10px;
}

.manic-rectangle {
    z-index: 2;
}

.manic-rectangle img {
    position: absolute;
    bottom: -4px;
    left: -6px;
    z-index: 1;
}

.manic-rectangle > span {
    z-index: 2;
    color: #030303;
}


@media screen and (max-width: 414px) {
    #body-content {
        max-width: 340px;
        padding: 0 4px;
    }

    #body-content p {
        font-size: 24px;
    }

    .f-md {
        font-size: 36px;
    }

    .f-lg {
        font-size: 42px;
    }

    .f-xl {
        font-size: 50px;
    }

    .manic-shine img {
        position: absolute;
        top: -44px;
        left: -28px;
        width: 120px;

    }

    .manic-line img {
        position: absolute;
        bottom: -16px;
        left: -20px;
        width: 320px;
    }

    .manic-emphasis-alt img {
        position: absolute;
        bottom: -10px;
        left: -20px;
        width: 160px;
    }

    .manic-emphasis img {
        position: absolute;
        bottom: -20px;
        left: -20px;
        width: 140px;
    }

    .manic-smiley img {
        /*position: absolute;*/
        /*!*bottom: -48px;*!*/
        /*!*right: -36px;*!*/
        /*width: 48px;*/
        /*left: 300px;*/
        /*top: -60px;*/
        display:none;
    }

    .manic-circle img {
        position: absolute;
        left: -30px;
        top: -40px;
    }

    .manic-circle-alt img {
        position: absolute;
        left: -40px;
        top: -40px;
        width: 200px;
    }


    .manic-rectangle {
        z-index: 2;
        display: block;
    }

    .manic-rectangle img {
        position: absolute;
        bottom: 0;
        left: -6px;
        z-index: 1;
        width: 316px;
    }

}

@media screen and (max-width: 767px)  and (orientation: landscape) {
    .manic-line img {
        position: absolute;
        bottom: -16px;
        left: -480px;
        width: 260px;
    }

    .manic-smiley img {
        /*position: absolute;*/
        /*!*bottom: -68px;*!*/
        /*!*right: -256px;*!*/
        /*width: 68px;*/
        /*bottom: 88px;*/
        /*right: -220px;*/
        display:none;
    }
    .manic-emphasis img {
        position: absolute;
        bottom: -20px;
        left: -20px;
        width: 164px;
    }

}

