/***

====================================================================
  General css style
====================================================================

***/
* {
    margin: 0;
    padding: 0;
}

body {
    font-family: acumin-pro, sans-serif;
    font-weight: 400;
    font-style: normal;
    background-image: url(../../static/media/noise.de360a52.png);
    background-size: cover;
    background-position: center;
    background-color: #030303;
}

a,
a:hover,
a:focus,
a:active {
    text-decoration: none;
    transition-duration: 500ms;
    outline: none;
}

li {
    list-style: none;
}

p {
    line-height: 1.9;
    color: #F4F4F4;
    font-size: 15px;
}

img {
    height: auto;
}

ul,
ol {
    margin: 0;
    padding: 0;
}

.relative {
    position: relative;
}

.content-main {
    height: 400px;
}

